.itemList{
    width: 85%;
    margin: 4rem auto;
}


.courses{
    display: flex;
    /*justify-content: center;*/
    flex-wrap: wrap;
    width: 100%;
    margin: 1.5rem auto;
}

.noItems{
    font-size: 2rem;
    color: red;
    text-align: center;
    margin: 3rem 0rem; 

}

.search__form{
    width: 70%;
    height: 2.3rem;
    display: flex;
    align-items: center;
   
   
}

.course__search{
    height: 100%;
    width: 100%;
    padding: 0 0 1.0 1rem;
    box-shadow: 2px 2px 4px rgba(0, 0,0,.4);
    border: none;
    transition: all .4s;
    margin-right: 2rem;
}

.not-items-input:focus{
    outline: 5px solid red;

}

.items-found-input:focus{
outline: 5px solid green;
}

.sort__input{
height: 100%;
margin-right: 1.2rem;
}

.order__input{
    height: 70%;
    margin-right: 1.2rem;

}